import React, { useState, useEffect } from "react";

import firebase from "firebase";

export default function ModalRateVideo({ userData }) {

  const [rateVideo, setRateVideo] = useState([]);

  useEffect(() => {
    setRateVideo(userData.momento1.evaluacion);
    // console.log(rateVideo);
  }, [userData.momento1.evaluacion]);

  // const [numberQuestion, setNumberQuestion] = useState(1);
  // const db = firebase.database();

  const handleSubmit = (e, moment) => {

    e.preventDefault();
    var rate = { evaluacion: rateVideo }
    const db = firebase.database().ref("respuestas").child(userData.id).child(moment);
    db.update(rate);

    alert("Se modificó con éxito")

    /* //console.log(i);
    //console.log(numberQuestion);
    e.preventDefault();
    //console.log(numberQuestion + 1);
    let auxData = { ...userData?.answers };
    auxData[i].rating =
      Object.values(rateVideo[i - 1]).reduce(
        (valorAnterior, valorActual, indice, vector) => {
          if (Object.values(rateVideo[i - 1]).length - 1 !== indice) {
            return parseInt(valorAnterior) + parseInt(valorActual);
          }
          return parseInt(valorAnterior);
        }
      ) / 4;
    auxData[i].comments =
      rateVideo[i - 1][Object.values(rateVideo[i - 1]).length];

    db.ref(`users/${userData.name}123456`).update({ answers: auxData },error=>{
      if (error) {
        // The write failed...
        alert("Ocurrió un error al guardar la respuesta");
      } else {
        alert("Se ha guardado la respuesta con éxito");
      }
    }); */
  };

  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    setRateVideo({ ...rateVideo, [name]: value });
    console.log(rateVideo);
  };

  const switchQuestionsForm = (i) => {
    return (
      <form onSubmit={(e) => handleSubmit(e, "momento1")}>
        <h3>Evalúa la respuesta</h3>
        <div className="form-group">
          <label htmlFor="exampleFormControlInput1" className="form-label">
            Competencia
          </label>
          <input
            value={rateVideo ? rateVideo?.competencia : ""}
            type="text"
            className="form-control"
            id="tipo"
            name="competencia"
            placeholder="Competencia..."
            onChange={(e) => handleChange(e)}
            required
          />
        </div>
        <div className="form-group">
          <label htmlFor="fluides">Puntaje</label>
          {/* {console.log(rateVideo.calificacion)} */}
          {/* <select
            defaultValue={rateVideo.calificacion || "default"}
            className="form-control"
            name="calificacion"
            onChange={(e) => handleChange(e)}
            required
          >
            <option value="default" disabled>
              Seleccione una calificación
            </option>
            <option value={1}>1</option>
            <option value={2}>2</option>
            <option value={3}>3</option>
            <option value={4}>4</option>
            <option value={5}>5</option>
          </select> */}
          <input
            value={rateVideo ? rateVideo?.calificacion : ""}
            type="text"
            className="form-control"
            id="tipo"
            name="calificacion"
            placeholder="Calificación..."
            onChange={(e) => handleChange(e)}
            required
          />
        </div>
        <div className="form-group">
          <label htmlFor="exampleFormControlTextarea1">Observaciones</label>
          <textarea
            value={rateVideo ? rateVideo.observacion : ""}
            className="form-control"
            id="exampleFormControlTextarea1"
            name="observacion"
            placeholder="Observación..."
            onChange={(e) => handleChange(e)}
            rows="3"
            required
          ></textarea>
        </div>
        <button type="submit" className="btn btn-primary btn-lg btn-block">
          Guardar
        </button>
      </form>
    );
  };

  return (
    <>
      <div
        className="modal fade bd-example-modal-xl"
        id="videoModal"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-xl" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">
                {`${userData?.usuario}`}
              </h5>
              <button
                type="button"
                className="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body overflow-auto">
              <h3>Etapas</h3>
              <div className="accordion" id="accordionExample">
                {/**Se recomienda poner las respuestas de los momentos en un array en la base de datos */}
                {!userData.momento1?.video || (
                  <div className="card" key={`userAcordion-${1}`}>
                    <div className="card-header" id="headingOne">
                      <h2 className="mb-0">
                        <button
                          className="btn btn-link"
                          type="button"
                          data-toggle="collapse"
                          data-target={`#collapse${1}`}
                          aria-expanded="true"
                          aria-controls={`collapse${1}`}
                        >
                          Momento #{1}
                        </button>
                      </h2>
                    </div>

                    <div
                      id={`collapse${1}`}
                      /* className={i == 1 ? "collapse show" : "collapse"} */
                      className="collapse"
                      aria-labelledby="headingOne"
                      data-parent="#accordionExample"
                    >
                      <div className="card-body">
                        <div className="container-fluid">
                          <div className="row">
                            <div className="col-md-6">
                              <div className="row">
                                <video
                                  className="w-100"
                                  controls
                                  key={userData.momento1?.video}
                                >
                                  <source
                                    src={
                                      userData.momento1?.video ||
                                      "https://media.w3.org/2010/05/sintel/trailer_hd.mp4"
                                    }
                                    type="video/webm"
                                  />
                                  Your browser does not support the video tag.
                                </video>
                              </div>
                              <div
                                className="row"
                                style={{ flexDirection: "column" }}
                              >
                                <p>Elementos seleccionados:</p>
                                <ul>
                                  {userData.momento1?.elementos_seleccionados?.map(
                                    (el, i) => {
                                      return (
                                        <li key={el.tittle}>{el.tittle}</li>
                                      );
                                    }
                                  )}
                                </ul>
                              </div>
                            </div>
                            <div className="col-md-6">
                              {switchQuestionsForm(1)}
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                )}
              </div>
              {/* ------------------------------------- */}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
