import React, { useState, useEffect } from "react";

//import modal
import ModalRateVideo from "../ModalRateVideo/ModalRateVideo";

export default function UserTab({ UsersData }) {
  const [UserData, setUserData] = useState({
    momento1: { 
      elementos_seleccionados:[], 
      evaluacion:[], 
      video: ""
    },
    usuario: "",
  });

  const changeUser = (user) => {
    //console.log(user);
    setUserData(user);
  };

  useEffect(() => {
    // console.log("cambiando UserData");
    // console.log(UserData)
  }, [UserData]);

  const rating = (jsonFirebase) => {
    if(jsonFirebase){
      return Object.values(jsonFirebase)
      .map((value) => value)
      .map((value) => value.rating)
      .reduce((accumulator, currentValue, indice, vector) => {
        let auxCurrentValue = 0;
        currentValue ? (auxCurrentValue = currentValue) : (auxCurrentValue = 0);
        return accumulator + auxCurrentValue;
      });
    }
    else{
      return "N/A"
    }
    
  };
  
  return (
    <>
      <ModalRateVideo
        userData={UserData}
      ></ModalRateVideo>
      <div className="table-responsive-lg">
        <table className="table">
          <thead className="thead-dark">
            <tr>
              <th scope="col">#</th>
              <th scope="col">Usuario</th>
              <th scope="col">Calificación</th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody>
            {Object.values(UsersData).map((value, i) => {
              //console.log(value)
              return (
                <tr key={"tr-" + i}>
                  <th scope="row">{i + 1}</th>
                  <td>{value.usuario}</td>
                  <td>{rating(value.answers)}</td>
                  <td>
                    <button
                      type="button"
                      className="btn btn-secondary btn-sm"
                      data-toggle="modal"
                      data-target="#videoModal"
                      onClick={() => {
                        //console.log(value);
                        changeUser(value);
                      }}
                    >
                      Calificar
                    </button>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    </>
  );
}
