import React, { useState, useEffect } from "react";
import Navbar from "../../componets/NavBar/Navbar";
import UserTab from "../../componets/User/UserTab";
//importo firebase
import * as firebase from "firebase";

export default function Home() {
  const [Users, setUsers] = useState({});
  // const [Questions, setQuestions] = useState({});

  useEffect(() => {
    const ref = firebase.database().ref();
    ref.child("respuestas").on("value", (snapshot) => {
      const users = snapshot.val();
      const usersList = [];
      for (let id in users) {
        usersList.push({ id, ...users[id] });
      }
      setUsers(usersList);
      // console.log(usersList);
    });

    //Momentos podría reemplazar a questions-por ahora no se enviará
    /* ref.child("questions")
      .on("value", (snapshot) => {
        setQuestions(snapshot.val());
      }); */
  }, []);

  return (
    <>
      <Navbar></Navbar>
      <div className="container mt-4">
        <h3>Calificaciones de usuarios</h3>
        <div className="row">
          <div className="col-md-12">
            <UserTab UsersData={Users}></UserTab>
          </div>
        </div>
      </div>
    </>
  );
}
